import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// nodejs library that concatenates classes
import classNames from "classnames";

// @material-ui/core components
import makeStyles from "@material-ui/core/styles/makeStyles";
import Button from "@material-ui/core/Button";

// core components

import buttonStyle from "../../assets/jss/components/buttonStyle.js";

const makeComponentStyles = makeStyles(() => ({
    ...buttonStyle
}));

// React.forwardRef to ra mt thnh phn (component) React, gip chuyn tip (forwards)
// thuc tnh (attribute) ref m n nhn c cho cc thnh phn khc bn di cy (tree).
// K thut ny khng tht s ph bin nhng li c bit hu ch trong hai tnh hung sau:

// - Chuyn tip refs ti cc thnh phn DOM (Forwarding refs to DOM components)
// - Chuyn tip refs trong cc thnh phn bc cao (Forwarding refs in higher-order-components)

// React.forwardRef chp nhn mt hm biu din (rendering function) l mt i s.
// React s gi n hm ny vi hai i s l props v ref.
// Hm ny nn tr v mt nt (node) React.

const RegularButton = React.forwardRef((props, ref) => {
    const {
        color,
        round,
        outline,
        children,
        fullWidth,
        disabled,
        simple,
        size,
        block,
        link,
        justIcon,
        className,
        ...rest
    } = props;

    const classes = makeComponentStyles();

    const btnClasses = classNames({
        [classes.button]: true,
        [classes[size]]: size,
        [classes[color]]: color,
        [classes.round]: round,
        [classes.fullWidth]: fullWidth,
        [classes.disabled]: disabled,
        [classes.simple]: simple,
        [classes.block]: block,
        [classes.link]: link,
        [classes.justIcon]: justIcon,
        [classes.outline]: outline,
        [className]: className
    });
    return (
        <Button {...rest} ref={ref} className={btnClasses}>
            {children}
        </Button>
    );
});

RegularButton.propTypes = {
    color: PropTypes.oneOf([
        "primary",
        "info",
        "success",
        "warning",
        "danger",
        "rose",
        "white",
        "facebook",
        "twitter",
        "google",
        "github",
        "transparent"
    ]),
    size: PropTypes.oneOf(["sm", "lg"]),
    simple: PropTypes.bool,
    round: PropTypes.bool,
    outline: PropTypes.bool,
    fullWidth: PropTypes.bool,
    disabled: PropTypes.bool,
    block: PropTypes.bool,
    link: PropTypes.bool,
    justIcon: PropTypes.bool,
    children: PropTypes.node,
    className: PropTypes.string
};

export default RegularButton;
