import React from "react";
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import '../../assets/scss/plugins/_plugin-react-slick.scss';
import { container } from '../../assets/jss/base';
// react component for creating beautiful carousel
import Carousel from "react-slick";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
// @material-ui/icons
import LocationOn from "@material-ui/icons/LocationOn";
// core components
import Card from "../Card/Card.js";

const useStyles = makeStyles({
    section: {
        padding: 0
    },
    container,
    marginAuto: {
        margin: 'auto',
    },
    deal: {
        display: 'flex',
        justifyContent: 'start',
        margin: 'auto',
    }
});

function Banner(props) {
    const classes = useStyles();
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true
    };

    const { banners } = props;

    return (
        <div className={classes.section}>
            <div className={classes.container}>
                <Grid container>
                    <Grid item xs={9} sm={9} md={5} className={classes.marginAuto}>
                        <Card>
                            <Carousel {...settings}>
                                {banners.map(banner => (
                                    <React.Fragment key={banner.id}>
                                        <img src={banner.imageUrl} alt="Banners" className="slick-image" />
                                        <div className="slick-caption">
                                            <h4>
                                                <LocationOn className="slick-icons" />
                                                Binh Duong, Viet Nam
                                            </h4>
                                        </div>
                                    </React.Fragment>
                                ))}
                            </Carousel>
                        </Card>
                    </Grid>
                    <Grid item xs={3} sm={3} md={5} className={classes.deal}>
                        <a
                            href="https://tiki.vn/khuyen-mai/uu-dai-cho-khach-hang-moi">
                            <img
                                height="150px"
                                src="https://salt.tikicdn.com/cache/w408/ts/banner/69/9a/36/dcff148edf2a3918a8ac974d4b75cb20.png"
                                alt="Hot Deal" />
                        </a>
                    </Grid>
                </Grid>
            </div>
        </div>
    );
}

Banner.defaultProps = {
    banners: [],
};

Banner.propsTypes = {
    banners: PropTypes.arrayOf(PropTypes.shape({
        id: PropTypes.string | PropTypes.number,
        imageUrl: PropTypes.string,
        link: PropTypes.string,
    }))
};

const mapStateToProps = ({ products: { banners } }) => ({
    banners
})

export default connect(mapStateToProps)(Banner);
